import Vue from 'vue'
import Router from 'vue-router'
import routes from './routes'
import hooks from './hooks' // 路由钩子

Vue.use(Router)

const router = new Router({
  // mode: 'history' // history模式，部署服务器需要单独配置404页面
})
router.addRoutes(routes)

hooks(router)

export default router



