<template>
  <div>
    <el-head>
    </el-head>
    <el-main>
      <el-card>
        <div slot="header">
          <el-row>
            <el-col :span="22">
              <el-breadcrumb separator=">">
                <el-breadcrumb-item
                  v-for="(thePath, index) in $route.meta.paths" :key="index"
                  :to="{ path: index === ($route.meta.paths.length-1) ? $route.path : thePath.url }">
                  {{thePath.name}}
                </el-breadcrumb-item>
              </el-breadcrumb>
            </el-col>
            <el-col :span="2">
              <!--<span>返回上一级</span>-->
            </el-col>
          </el-row>

        </div>

        <router-view></router-view>

      </el-card>
    </el-main>
  </div>
</template>

<script>
  import footDown from '@/components/footer/foot'
  import elHead from '@/components/header/elHead'
  export default {
    name: 'main-base',
    data () {
      return {
      }
    },
    components: {
      elHead,
      footDown
    }
  }
</script>

<style scoped>

</style>



